<template>
  <div class="me container">
      <van-nav-bar fixed title="我"></van-nav-bar>
      <div class="me-user" @click="toDetail">
        <div class="me-user-img">
          <span style="color:#666;">
            {{userInfo.company}}
            <van-icon class="iconfont" class-prefix='icon' color="#47BE98" slot="icon" name='dunpai'></van-icon>
            </span>
          <img :src="userInfo.avatar" alt="">
        </div>
        <div class="me-user-name">
          <div class="me-user-name-top">{{userInfo.name}}</div>
          <div class="me-user-name-bottom">{{userInfo.dept}}</div>
        </div>
      </div>
      <div class="me-common border" @click="editPwd">
        <van-icon name="setting-o" color='#99bf50' class="me-common-icon"/>
        <span>修改密码</span>
      </div>
      <div class="me-common border" @click="toAbout">
        <van-icon name="info-o" color='#b97de4' class="me-common-icon"/>
        <span>关于</span>
      </div>
  </div>
</template>

<script>
import { NavBar, Icon } from 'vant'
import defaultAvatar from '@/assets/img/default-avatar.png'
// import { getUserInfo } from '@/api/me'

export default {
  components: {
    [NavBar.name]: NavBar,
    [Icon.name]: Icon
  },
  data () {
    return {
      userInfo: {
        avatar: defaultAvatar,
        name: '',
        code: '',
        company: '',
        dept: ''
      }
    }
  },
  methods: {
    toDetail () {
      this.$router.push({
        path: '/me/detail',
        query: {}
      })
    },
    editPwd () {
      this.$router.push({
        path: '/me/edit-pwd',
        query: {}
      })
    },
    toAbout () {
      this.$router.push({
        path: '/me/about',
        query: {}
      })
    }
  },
  created () {
    // getUserInfo().then(data => {
    //   Object.keys(this.userInfo).forEach(key => {
    //     this.userInfo[key] = data[key]
    //   })
    // })

    // 下面的是测试数据
    const data = {
      name: '周杰伦',
      code: '31312313',
      company: '中通服咨询设计研究院',
      dept: '信息技术及SDN研发事业部',
      avatar: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2156765786,3889412097&fm=26&gp=0.jpg'
    }
    Object.keys(this.userInfo).forEach(key => {
      this.userInfo[key] = data[key]
    })
  }
}
</script>

<style  scoped>

.me-user{
  height: 160px;
  margin: 15px;
  padding: 15px;
  background-color: #fff;
  border-radius: 3px;
}

.me-user-img{
  display: flex;
  justify-content: space-between;
}

.me-user-img>img {
  width: 70px;
  height: 70px;
  border-radius: 5px;
}

.me-user-name{
  padding-top:10px ;
}
.me-user-name-top{
  padding-bottom: 15px;
  font-weight: bold;
  font-size: 18px;
}
.me-user-name-bottom{
  font-size: 13px;
  color: #999;
}
.me-common{
  background-color: #fff;
  height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}
.me-common::before{
  content: '';
  width: 6px;
  height: 6px;
  border: 2px solid #ccc;
  position: absolute;
  top: 50%;
  right: 15px;
  border-left-color: transparent;
  border-bottom-color: transparent;
  transform: translateY(-5px) rotate(45deg);
}

.me-common-icon{
  padding:0 15px;
  font-size: 20px;
}
</style>



